}
.title-wrapper .title{
  font-size: 25px;
  margin-bottom: 0px;
}
.about-content-wrapper{
  display: -ms-flexbox;
  display: flex;
  margin: 45px 60px;

}
.profile-image-wrapper{
  width: 50%;
  padding: 0px 20px;
}
.profile-image-wrapper img{
  width: 100%;
}
.about-info-wrapper{
