  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.about-text{
  font-size: 1.5em;
  text-align: justify;
  text-indent: 2em;
}
  .contact-wrapper{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .contact-wrapper img {
    height: 4em;
  }

@media screen and  (max-width: 700px) {
  .about{
    padding-top: 3.5em;
  }
  .about-content-wrapper{
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    margin: 1em 2em;
  }
  .contact-wrapper{
    display: -ms-flexbox;
    display: flex;
  }
  .contact-wrapper img {
    height: 3em;
  }
  .profile-image-wrapper{
    width: 80%;
    margin-bottom: 1em;
  }
  .about-info-wrapper{
    width: 80%;
  }
  .about-text{
